<script>
  import Wheel from "../components/Wheel.svelte";
  import pop from "../components/Confetti.js";

  let result = "";

  function handleMessage(event) {
    result = event.detail.text;
    pop("confetti")
  }
</script>

<main class="h-100">

  <div class="w-full mx-auto px-auto flex">
    <Wheel on:message={handleMessage} />
    <div id="confetti" class="m-auto text-6xl">{result}</div>
  </div>

</main>
